<template>
  <div id="app">
	  <!-- <uc-field></uc-field> -->
	  <!-- <swipe-test></swipe-test> -->
	  <!-- <Loading/> -->
	  <!-- <uc-cell-test></uc-cell-test> -->
	  <loading v-show='$root.bLoading'></loading>
	  <app-header v-show='$root.bNav'></app-header>
	  <!-- <home></home> -->
	  <!-- <detail></detail> -->
	  <!-- <user></user> -->
	  <!-- <login></login> -->
	    <router-view></router-view>
	  <app-footer v-show='$root.bFoot'></app-footer>
  </div>
</template>

<script>
	// 
// import SwipeTest from './components/swipe/swipe-test.vue'
	// 
// import Loading from './components/loading/loading.vue'
	// 
// import UcCellTest from './components/uc-cell/uc-cell.test.vue'
import AppHeader from './header.vue'
import home from '../pages/home.vue'
import detail from '../pages/detail.vue'
import AppFooter from './footer.vue'
import User from '../pages/user.vue'
import Login from '../pages/login.vue'
import loading from  '../components/loading/loading.vue'
export default {
  name: 'app',
  data(){
	  return {

	  }
  },
  components: {
     AppHeader,
	 AppFooter,
	 home,
	 detail,
	 User,
	 loading
	 // Login
  	// Loading,
	// UcCellTest
  },
  watch:{
	  $route:{
		  handler(nextValue){
			  let path=nextValue.path;
			  if(/home|follow|column/.test(path)){
				  this.$root.bNav=this.$root.bFoot=true
			  }
			  if(/detail|login|reg/.test(path)){
				  this.$root.bNav=this.$root.bFoot=false
			  }
			  if(/user/.test(path)){
				  this.$root.bNav=false;
				  this.$root.bFoot=true
			  }
		  },
		  immediate:true 
	  }
  }
}
</script>

