<template>
  <div class="detail paddingTop">
    
    <div class="detail__list">
      <uc-cell
        v-for="(item,index) of column"
        :key="item._id"
		:index="index"
        :data="item"
        :to="`/detail/${item._id}?collectionName=column`"
      />
    </div>
    

  </div>
</template>

<script>
  import UcCell from '../components/uc-cell/uc-cell.vue'
  
  export default {
    
    data(){
      return {
        column:[]
      }
    },
    components:{
      UcCell
    },
    
    mounted(){
     this.$axios({
		 url:'/news/column',
		 params:{
			 _limit:15
		 }
	 }).then(
		 res=>this.column=res.data.data
	 )
    }
  }
</script>

<style scoped>
  .detail__list{
    padding: 0 .2rem;
  }
</style>
