//axiostoken   |  | loading |  | axiosurl/api
import axios from 'axios';
import Vue from 'vue';
import router from './router.js'
import vm from '../main.js'
//
// 
axios.interceptors.request.use(function(config) {
  //config   axios(config)
  
  // token
  let token = window.localStorage.getItem('user');//{err:0,msg:'',data:xx,token:string}
  token = token ? JSON.parse(window.localStorage.getItem('user')).token : ''
  // axiosheaders
  config.headers={
    token: token,
  },
  // config.baseURL axiosurl/api 
  // vue-devtools$routequery
  config.baseURL =  '/api';
  vm.bLoading=true
  return config;// 
}, function(error) {
  // 
  return Promise.reject(error);
});

//    
axios.interceptors.response.use(function(response) {
  //response  axiosres
  //    res.status   res.data.err == 2
  //tokenlogin,
  let currentRoute = router.currentRoute.fullPath;//string
  if(response.data.err===2 && !currentRoute.includes('/login')){
    router.replace({
      path:'/login',
	  // useruser 
      query:{p:currentRoute}
    })
  }
  vm.bLoading=false
  return response;//
}, function(error) {
  // 
  return Promise.reject(error);
});

// axiosVuevueimport axios from 'axios'; this.$axios()this
Vue.prototype.$axios=axios;
window.axios=axios;//
export default axios;