<template>
  <div class="follow paddingTop"> 
    <div class="follow">
      <uc-cell
        v-for="(item,index) of follow"
        :key="item._id"
        :index="index"
        :data="item"
        :to="`/detail/${item._id}?collectionName=follow`"
      />
        <!-- :to="`/detail/${item._id}`" -->
    </div>
  </div>
</template>

<script>
  // import UcSwiper from '../components/uc-swiper/uc-swiper.vue'
  // import UcCell from '../components/uc-cell/uc-cell.vue'
  import UcCell from '../components/uc-cell/uc-cell.vue'
  export default {
    
    data(){
      return {
        follow:[
          // {_id:1,title:'xx1',des:'xx2'},
          // {_id:2,title:'xx2',des:'xx2'},
          // {_id:3,title:'xx3',des:'xx2'},
        ]
      }
    },
    components:{
      UcCell
    },
    
    mounted(){
		this.$axios({
			url:'news/follow',
			params:{
				_limit:30
			}
		}).then(res=>{
			this.follow=res.data.data
		})
    }
  }
</script>

<style scoped>
  .home__list{
    padding: 0 .2rem;
  }
</style>
