<template>
	<div class="content">
		<uc-nav bg-color="transparent" arrow="gray" :border-width="0"></uc-nav>
		
		<h1 id="img" @click="upload"></h1>
		<input type="file" id="icon" style="display: none;">
		
		<div class="login-box">
			<p class="lsolid"></p>
			<div class="login">
				<router-link to="/login"></router-link>
				<!-- <a href="login_m.html"></a> -->
				<span></span>
				<router-link to="/reg"></router-link>
			</div>
			<p class="rsolid"></p>
		</div>
		<ul>
			<uc-field label="" v-model="username"></uc-field>
			<uc-field label="" type="password" v-model="password"></uc-field>
			{{mess}}
		</ul>
		<div class="footbox">

			<uc-button style="margin-top: .8rem;" text=" " @click="reg"></uc-button>
			<a href="javascript:;" class="tishi"></a>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'reg',
		data() {
			return {
				username: '',
				password: '',
				mess: ''
			}
		},
		methods: {
			upload() {
				let inputfile = document.getElementById('icon');
				inputfile.click()
			},

			reg() {
				let fileInput = document.getElementById('icon');
				console.log(fileInput.files)
				let formData = new FormData();
				formData.append('username', this.username);
				formData.append('password', this.password);
				formData.append('icon', fileInput.files[0]);
				formData.append('nikename', '')
				this.$axios({
					
					url: 'reg',
					method: 'post',
					// data: {
					// 	username: this.username,
					// 	password: this.password
					// }
					data:formData
				}).then(res => {
					if (res.data.err == 0) {
						this.$router.push('/login');
					} else {
						this.mess = res.data.msg
					}
				})
			}
		}
	}
</script>


